.responsive {
    width: 100%;
    height: auto;
    max-width: 700px;
}

img {
    width: 100%;
    height: auto;
    max-width: 700px;
}

.home,
.adoption,
form,
.animal {
    margin: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

button {
    display: block;
    margin: 10px;
    width: 90px;
    height: 50px;
    padding: 3px;
}

button:hover {
    cursor: pointer;
}

a {
    text-decoration: none;
}